$primary_color: #3f51b5;

.background{
    background-color: #222;
    height: 100%;
    width: 100%;
}

.heading{
    margin-top: 4rem;
    padding-top: 10rem;
}

.description{
font-size: 1.3rem;
margin-top: 2rem;
}

.heading, .description{
    text-align: center;
    color: #fff;
}

.buttonsDiv{
    width: 100%;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    margin-top: 2rem;
    & button:first-of-type, & button:last-of-type{
        font-size: 1.5rem;
        display: block;
    }
    & button:first-of-type{
        margin-right: 1rem;
    }
    & button:last-of-type{
        margin-left: 1rem;
    }
}

.whyContainer{
    margin-top: 10rem;
    padding: 0 3rem;
}
.whyGlex{
    color: #fff;
    font-size: 1.3rem;
    padding: 2rem 3rem;
}

.card{
    height: auto;
    width: 35rem;
    background: transparent!important;
    box-shadow: none!important;
   & > *{
    color: #fff!important;
   }
}

.grid{
    width: 80%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 4rem auto;
}

.socialProfiles{
    margin-top:20rem;
    padding-bottom: 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
}
