.wrapper {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: "open sans", sans-serif;
  color: var(--black);
  padding-bottom: 2rem;
}

.title {
  text-transform: uppercase;
  margin: 3rem auto 0 auto;
  font-size: 2rem;
}

.bookmark-card {
  height: auto;
  width: 45rem;
  display: flex;
  align-items: center;
  background-color: #fff;
  border-radius: 1.5rem;
  margin: 2rem 1rem 0 1rem;
  box-shadow: 0rem 1.5rem 2rem rgba(0, 0, 0, 0.2);
  padding: 1rem;
  position: relative;
}

.blog-image img {
  height: 14rem;
  width: 18rem;
  border-radius: 1.2rem;
}

.info {
  padding-left: 1.5rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.blog-header {
  overflow: hidden;
}
.blog-header p {
  font-family: "Poppins", sans-serif;
  color: #74899e;
  font-size: 1.1rem;
  margin: 0;
}

.blog-header h3 {
  font-size: 1.5rem;
  margin: 0;
  margin-bottom: 2rem;
}
.author-info {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.author-info p {
  font-size: 1.1rem;
  margin: 0;
  margin-left: 1rem;
  font-weight: bold;
}

.author-info p:nth-child(2) {
  color: #74899e;
}
.author-info img {
  height: 4rem;
  width: 4rem;
  border-radius: 100%;
}

.delete-icon {
  position: absolute;
  top: 1rem;
  right: 1rem;
  font-size: 2rem;
  color: #74899e;
  cursor: pointer;
}

.delete-icon:hover {
  color: var(--light-blue);
  transform: scale(1.2);
  transition: all 0.2s;
}

.pagination {
  margin-top: 2rem;
}

@media screen and (max-width: 500px) {
  .bookmark-card {
    width: 32rem;
    margin-left: 0;
  }
  .blog-image img {
    width: 14rem;
    height: 12rem;
  }
  .blog-header h3 {
    font-size: 1.3rem;
  }
}

@media screen and (max-width: 350px) {
  .bookmark-card {
    width: 28rem;
  }
  .blog-image img {
    width: 14rem;
  }
  .blog-header h3 {
    font-size: 1.3rem;
  }
}
