.authorImage{
    height:5rem;
    width: 5rem;
    border-radius: 100%;
}

.authorIntro{
    display: flex;
    align-items: center;
    justify-content: center;
}

.authorAndDate{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 2rem auto;
}

.blogCard{
    width: 75%;
    height: auto;
    margin-top:1rem;
    border-radius:1.2rem;
}

.blogImageDiv{
    position: relative;
    margin-bottom: 1.5rem;
}
.blogImage{
    width: 100%;
    height: 40rem;
    border-radius: 1.2rem;
    object-fit:cover;
}

.badge{
    position: absolute;
    bottom: 1rem;
    left:1rem;
    padding:1.5rem 2rem;
    border-radius: 5rem;
    align-items: center;
    justify-content: center;
    display: inline-flex;
}

.description{
    font-size: 1.4rem;
    color:rgb(6, 6, 31);
}

.insights{
 display: flex;
 align-items: center;
 justify-content: space-between;
 margin: 1.5rem auto;
}

.recentposts{
    display: flex;
    flex-direction: column;
    align-items: center;
    // justify-content: center;
}

.postcard{
    height: 20rem;
    width: 30rem;
    border-radius: 1.6rem;
    position: relative;
    margin-top: 2rem;
}

.recentPostImg{
    height: 100%;
    width: 100%;
    border-radius: inherit;
}

.postCardDiv{
    position: absolute;
    height: 100%;
    width: 100%;
    background-color: rgba(0,0,0,0.6);
    top: 0;
    left: 0;
    border-radius: 1.6rem;
    display:flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    padding: 0 1.5rem;
}

.commentSection{
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    margin-top: 3rem;
    width: 100%;
    padding-left: 10rem;
}

.commentDiv{
    display: flex;
    justify-content: flex-start;
    margin-top: 5rem;
    width:100%;
}
.commentDiv img{
    height: 6rem;
    width: 6rem;
    border-radius: 100%;
}

.commentContent, .replyContent{
    margin-left: 3rem;
    background-color: #e2e8f0;
    padding: 2rem 3rem;
    border-radius: 0.6rem;
    position: relative;
    width: 60%;
}

 .commentContent::before{
    content: "";
    position: absolute;
    background: #e2e8f0;
    height: 1.3rem;
    width: 4rem;
    left: -2.6rem;
    border-radius: 20% 0 0 80%;
    transform: rotateZ(-20deg);
}

.replyContent{
    margin-right:3rem;
}
.replyContent::before{
    content: "";
    position: absolute;
    background: #e2e8f0;
    height: 1.3rem;
    width: 4rem;
    right: -2.6rem;
    border-radius: 0 20% 80% 0;
    transform: rotateZ(20deg);
}

.commentor{
    width: 100%;
    margin-top: -1rem;
    margin-bottom: 1.2rem;
    border-bottom: 0.1rem solid rgb(128, 128, 128)
}

.commentText{
    display: flex;
     justify-content: space-between;
     width: 100%;
}
@media screen and (max-width: 760px){
    .commentSection{
        padding-left: 0;
    }
}