@import url("https://fonts.googleapis.com/css2?family=Poppins&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Nunito&family=Open+Sans&display=swap");

*
{
    margin:0;
    padding:0;
    box-sizing: border-box;
}

:root {
  --black: #262b34;
  --dark-blue: #0837ff;
  --light-blue: #1b5ad0;
  --lighter-blue: #6b8cb7;
  --dark-grey: #8e99b4;
  --light-grey: #a5b1cb;
  --lighter-grey: #b9c6db;
  --white: #f0f3f7;
}

html {
  font-size: 10px;
}

button:focus{
  outline:0;
}
body {
  background-color: var(--white);
  height: 100vh;
  width: 100vw;
  overflow-x: hidden;
}
