@mixin flex($display_val, $justify_val, $flex_direc_val) {
  display: $display_val;
  align-items: center;
  justify-content: $justify_val;
  flex-direction: $flex_direc_val;
}

@mixin full_dimensions {
  height: 100%;
  width: 100%;
}

@mixin blue-button {
  border-radius: 5rem;
  background: #3f51b5 !important;
  color: #fff;
  padding: 1rem 2rem;
  margin: 2rem auto 1rem auto;
  text-decoration: none;
  &:hover {
    background-color: #2d42b6 !important ;
    transition: $transition;
  }
}
$transition: all 0.3s;
$border_radius: 1.6rem;

.carousel {
  background: #e3e4f8;
}
.carousel li {
  width: auto !important;
  padding-top: 2rem;
}
.carousel strong {
  background: transparent;
}
.carousel button:focus {
  outline: none;
}
.postsGrid {
  padding-bottom: 30px;
  @include flex(flex, center, column);
}
.card {
  @include flex(flex, start, row);
  background-color: #fff;
  width: 70rem;
  height: 32rem;
  // margin: auto;
  margin-top: 3rem;
  border-radius: $border_radius;
  box-shadow: 0.5rem 2rem 4rem rgba(0, 0, 0, 0.2);
  transition: $transition;
}

.card:hover {
  box-shadow: 0.5rem 0.5rem 1rem rgba(0, 0, 0, 0.2);
}
.cardImage {
  @include full_dimensions;
  min-width: 30rem !important;
  max-width: 30rem !important;
  height: 100%;
  position: relative;
  border-radius: inherit;
}

.blogImage {
  @include full_dimensions;
  object-fit: cover;
  border-radius: $border_radius 0 0 $border_radius;
  min-width: 30rem !important;
  max-width: 30rem !important;
}

.cardCategory {
  position: absolute;
  left: 1.5rem;
  bottom: 1.5rem;
  display: flex;
}

.cardCategory,
.badge {
  padding: 1.5rem 2rem;
  border-radius: 5rem;
  align-items: center;
  justify-content: center;
}

.badge {
  display: inline-flex;
  margin-bottom: 1.2rem;
  margin-right: 0.3rem;
  margin-left: 0.3rem;
}

.cardDescription {
  padding-left: 3rem;
  padding-right: 1rem;
}

.cardButton {
  @include blue-button;
  display: inline-block;
}

.cardInfo {
  margin: 1.2rem auto 0 auto;
}

.cardInfo,
.cardAuthor {
  @include flex(flex, space-between, row);
}

.cardAuthor img {
  height: 3.5rem;
  width: 3.5rem;
  border-radius: 100%;
}
.cardIcons {
  @include flex(inline-flex, space-between, row);
  margin-right: 1rem;
}

.iconDiv {
  @include flex(inline-flex, center, row);
  margin: 0 1rem;
}

.profileGrid {
  @include flex(flex, start, column);
  padding-top: 10rem;
  padding-bottom: 2rem;
}

.publishLink {
  @include blue-button;
}
.publishLink:hover {
  background-color: #2d42b6 !important ;
  transition: all 0.3s;
}
.profileCard {
  @include flex(flex, center, column);
  height: 30rem;
  width: 25rem;
  background: url("https://picsum.photos/300/250?greyScale&random=1.webp");
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
  border-radius: $border_radius;
  margin-top: 2rem;
}

.profileCard::after {
  @include full_dimensions;
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.8);
  border-radius: inherit;
}

.profileCard > * {
  z-index: 2;
}
.profileCardImg {
  height: 8rem;
  width: 8rem;
  border-radius: 100%;
  border: 0.35rem solid #fff;
}
.profileCardImg img {
  @include full_dimensions;
  border-radius: inherit;
}

.profileCardName {
  @include flex(flex, center, column);
}

.profileCardCounts {
  @include flex(flex, space-around, row);
  width: 100%;
  height: auto;
  padding: 1rem 0;
  background-color: rgb(44, 44, 180);
  margin: 2rem 0;
}

.profileCardCounts div {
  @include flex(flex, center, column);
}

.followButton {
  background-color: transparent;
  border: 0.2rem solid #fff;
  padding: 1rem 2rem;
  color: #fff;
  cursor: pointer;
}
.followButton:hover {
  color: rgba(0, 0, 0, 0.8);
  background: #fff;
  transition: $transition;
}

.featured {
  height: auto;
  width: 25rem;
  margin: 4rem auto 1.5rem auto;
}

.featuredList {
  background: #fff;
  border-radius: 1rem;
  list-style-type: none;
  margin-top: 1rem;
  padding: 2rem;
}

.featuredListItem {
  padding: 1rem 0 0.1rem 0;
  border-bottom: 0.05rem solid rgba(0, 0, 0, 0.2);
}

.socialProfiles {
  margin: 2rem auto;
  & > svg:hover {
    cursor: pointer;
    opacity: 0.6;
  }
}

.tags {
  width: 25rem;
  margin: 2rem auto;
}
@media screen and (max-width: 700px) {
  .card {
    width: 40rem;
    height: auto;
    flex-direction: column;
    margin: 3rem 0;
  }

  .blogImage {
    border-radius: $border_radius $border_radius 0 0;
  }
  .cardDescription {
    padding: 2rem 1.5rem;
  }
}
@media screen and (max-width: 500px) {
  .card {
    width: 35rem;
  }
}

@media screen and (max-width: 395px) {
  .card {
    width: 30rem;
  }
}
